import { useForm } from "react-hook-form";
import DetailWrap from "../../components/DetailWrap";
import Name from "../../components/Name";
import { useEffect, useState } from "react";
import Title from "../../components/Title";
import styled from "styled-components";
import SubmitButton from "../../components/SubmitButton";
import { Document, Packer, Paragraph, TextRun } from "docx";
import { saveAs } from "file-saver";

const InputText = styled.input`
  all: unset;
  padding: 5px;
  width: 50%;
  border: 1px solid #000;
  margin-bottom: 20px;
`;

const Jurisdiction = (): JSX.Element => {
  const [data, setData] = useState<any>(null);

  const { register, handleSubmit, reset } = useForm<any>();

  const onSubmit = (data: any) => {
    setData({
      consensual1_name: `${data.consensual1_name}`,
      consensual1_id: `${data.consensual1_id}`,
      consensual1_address: `${data.consensual1_address}`,
      consensual1_number: `${data.consensual1_number}`,
      consensual2_name: `${data.consensual2_name}`,
      consensual2_id: `${data.consensual2_id}`,
      consensual2_address: `${data.consensual2_address}`,
      consensual2_number: `${data.consensual2_number}`,
      conclusion_date: `${data.conclusion_date}`,
      conclusion_detail: `${data.conclusion_detail}`,
      courthouse: `${data.courthouse}`,
      attach: `${data.attach}`,
      creation_date: `${data.creation_date}`,
    });
    reset();
  };

  useEffect(()=>{
    if (data) {
      const doc = new Document({
        sections: [
          {
            children:[
              new Paragraph({
                alignment: "center",
                children: [
                  new TextRun({
                    text: "    ",
                    bold: true,
                    size: 30,
                  }),
                ],
              }),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph({
                text: `${data.consensual1_name} (${data.consensual1_id})`,
              }),
              new Paragraph({
                text: `${data.consensual1_address} ( ${data.consensual1_number})`,
              }),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph({
                text: `${data.consensual2_name} (${data.consensual2_id})`,
              }),
              new Paragraph({
                text: `${data.consensual2_address} ( ${data.consensual2_number})`,
              }),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph({
                text: `     ${data.conclusion_date}  ${data.conclusion_detail}   ${data.courthouse} 1    .`,
              }),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph({
                text:`     : ${data.attach}    1`,
              }),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph({
                text: `${data.creation_date}`,
                alignment: "center",
              }),
              new Paragraph(""),
              new Paragraph({
                text:`  ${data.consensual1_name} (  )`,
                alignment:"right",
              }),
              new Paragraph({
                text:`${data.consensual2_name} (  )`,
                alignment:"right",
              }),
            ],
          },
        ],
      });
      Packer.toBlob(doc).then((bolb)=>{
        saveAs(bolb, ".docx");
      });
    };
  }, [data]);

  

  return (
    <DetailWrap submitHandler={handleSubmit(onSubmit)}>
      <Name></Name>
      <Title>1 </Title>
      <InputText type="text" {...register("consensual1_name")}/>
      <Title>1 </Title>
      <InputText type="text" {...register("consensual1_id")}/>
      <Title>1 </Title>
      <InputText type="text" {...register("consensual1_address")}/>
      <Title>1 </Title>
      <InputText type="text" {...register("consensual1_number")}/>
      <Title>2 </Title>
      <InputText type="text" {...register("consensual2_name")}/>
      <Title>2 </Title>
      <InputText type="text" {...register("consensual2_id")}/>
      <Title>2 </Title>
      <InputText type="text" {...register("consensual2_address")}/>
      <Title>2 </Title>
      <InputText type="text" {...register("consensual2_number")}/>
      <Title>  </Title>
      <InputText type="date" {...register("conclusion_date")}/>
      <Title> </Title>
      <InputText type="text" {...register("conclusion_detail")} placeholder=") "/>
      <Title> </Title>
      <InputText type="text" {...register("courthouse")}/>
      <Title> </Title>
      <InputText type="text" {...register("attach")}/>
      <Title></Title>
      <InputText type="text" {...register("creation_date")}/>
      <SubmitButton />
    </DetailWrap>
  );
};
export default Jurisdiction;
