import {
  Document,
  Packer,
  Paragraph,
  Table,
  TableCell,
  TableRow,
  TextRun,
  WidthType,
} from "docx";
import { saveAs } from "file-saver";
import DetailWrap from "../../components/DetailWrap";
import Name from "../../components/Name";
import { useEffect, useRef, useState } from "react";
import { useForm } from "react-hook-form";
import SubmitButton from "../../components/SubmitButton";
import Heading from "../../components/Heading";
import Title from "../../components/Title";
import styled from "styled-components";
import HeadingWrap from "../../components/HeadingWrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus } from "@fortawesome/free-solid-svg-icons";

const InputText = styled.input`
  all: unset;
  padding: 5px;
  width: 50%;
  border: 1px solid #000;
`;

const SelectWrap = styled.div`
  width: 65%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
  select {
    all: unset;
    width: 45%;
    padding: 10px;
    border: 1px solid #000;
    -webkit-appearance: auto;
    -moz-appearance: auto;
    appearance: auto;
  }
`;

const PlusButton = styled.div`
  font-style: normal;
  width: 30px;
  height: 30px;
  background-color: #777;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  margin-left: 10px;
  border-radius: 10px;
  font-size: 20px;
`;

const Textarea = styled.textarea`
  all: unset;
  width: 100%;
  border: 1px solid #000;
  padding: 5px;
  margin-bottom: 20px;
  white-space: pre-wrap; /*   */
  word-wrap: break-word; /*    */
  overflow-x: hidden; /*    */
`;

const CivilLitigationComplaint = (): JSX.Element => {
  const [data, setData] = useState<any>();
  const whatCattle = useRef<any>();
  const [cattle, setCattle] = useState<string>("claim");
  const [effectPlus, setEffectPlus] = useState<number[]>([1]);
  const [causePlus, setCausePlus] = useState<number[]>([1]);
  const [proofPlus, setProofPlus] = useState<number[]>([1]);
  const [effectElement, setEffectElement] = useState<any>(null);
  const [causeElement, setCauseElement] = useState<any>(null);
  const [proofElement, setProofElement] = useState<any>(null);
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm<any>();

  const changeHandler = () => {
    setCattle(`${whatCattle?.current?.value}`);
  };

  const effectHandler = () => {
    const newEffect = effectPlus.length + 1;
    setEffectPlus([...effectPlus, newEffect]);
  };

  const causeHandler = () => {
    const newCause = causePlus.length + 1;
    setCausePlus([...causePlus, newCause]);
  };

  const proofHandler = () => {
    const newProof = proofPlus.length + 1;
    setProofPlus([...proofPlus, newProof]);
  };

  const onSubmit = (data: any) => {
    setData({
      plaintiff_name: `${data.plaintiff_name}`,
      plaintiff_id: `${data.plaintiff_id}`,
      plaintiff_address: `${data.plaintiff_address}`,
      plaintiff_number: `${data.plaintiff_number}`,
      plaintiff_phone: `${data.plaintiff_phone}`,
      plaintiff_fax: `${data.plaintiff_fax}`,
      plaintiff_mail: `${data.plaintiff_mail}`,
      lawyer_name: `${data.lawyer_name}`,
      lawyer_address: `${data.lawyer_address}`,
      lawyer_number: `${data.lawyer_number}`,
      defendant_name: `${data.defendant_name}`,
      defendant_id: `${data.defendant_id}`,
      defendant_address: `${data.defendant_address}`,
      defendant_number: `${data.defendant_number}`,
      defendant_phone: `${data.defendant_phone}`,
      defendant_fax: `${data.defendant_fax}`,
      defendant_mail: `${data.defendant_mail}`,
      cattle: `${data.cattle}`,
      effect: effectPlus.map((value) => `${data["effect" + value]}`), 
      //!JavaScript data["effect" + value]    .(=========********==========)
      cause: causePlus.map((value) => `${data["cause" + value]}`),
      proof: proofPlus.map((value) => `${data["proof" + value]}`),
      creation_date: `${data.creation_date}`,
      courthouse: `${data.courthouse}`,
    });

    reset();
  };
  
  useEffect(() => {
    if (data) {
      setEffectElement(
        data?.effect.map(
          (value: any, index: number) =>
            new Paragraph({
              text: `${index + 1}. ${value || ""}`,
            })
        )
      );
      setCauseElement(
        data?.cause.map(
          (value: any, index: number) =>
            new Paragraph({
              text: `${index + 1}. ${value || ""}`,
            })
        )
      );
      setProofElement(
        data?.proof.map(
          (value: any, index: number) =>
            new Paragraph({
              text: `${index + 1}. ${value || ""}`,
            })
        )
      );
    }
  }, [data]);

  useEffect(() => {
    if (effectElement && causeElement && proofElement) {
      const doc = new Document({
        sections: [
          {
            children: [
              new Paragraph({
                alignment: "center",
                children: [
                  new TextRun({
                    text: "    ",
                    bold: true,
                    size: 30,
                  }),
                ],
              }),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Table({
                width: {
                  size: 100,
                  type: WidthType.PERCENTAGE,
                },
                borders: {
                  top: { style: "none" },
                  bottom: { style: "none" },
                  left: { style: "none" },
                  right: { style: "none" },
                  insideHorizontal: { style: "none" },
                  insideVertical: { style: "none" },
                },
                rows: [
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "top",
                        rowSpan: 6,
                        children: [
                          new Paragraph({
                            text: "    ",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 80,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "top",
                        columnSpan: 2,
                        children: [
                          new Paragraph({
                            text: `${data.plaintiff_name} (${data.plaintiff_id})`,
                          }),
                        ],
                      }),
                    ],
                  }),
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 80,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "top",
                        columnSpan: 2,
                        children: [
                          new Paragraph({
                            text: `${data.plaintiff_address} (: ${data.plaintiff_number})`,
                          }),
                        ],
                      }),
                    ],
                  }),
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 80,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "top",
                        columnSpan: 2,
                        children: [
                          new Paragraph({
                            text: `   ${data.lawyer_name}`,
                          }),
                        ],
                      }),
                    ],
                  }),
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 80,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "top",
                        columnSpan: 2,
                        children: [
                          new Paragraph({
                            text: `${data.lawyer_address} (${data.lawyer_number})`,
                          }),
                        ],
                      }),
                    ],
                  }),
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 40,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "top",
                        children: [
                          new Paragraph({
                            text: `: ${data.plaintiff_phone}`,
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 40,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "top",
                        children: [
                          new Paragraph({
                            text: `: ${data.plaintiff_fax}`,
                          }),
                        ],
                      }),
                    ],
                  }),
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 80,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "top",
                        columnSpan: 2,
                        children: [
                          new Paragraph({
                            text: `: ${data.plaintiff_mail}`,
                          }),
                        ],
                      }),
                    ],
                  }),
                ],
              }),
              new Table({
                width: {
                  size: 100,
                  type: WidthType.PERCENTAGE,
                },
                borders: {
                  top: { style: "none" },
                  bottom: { style: "none" },
                  left: { style: "none" },
                  right: { style: "none" },
                  insideHorizontal: { style: "none" },
                  insideVertical: { style: "none" },
                },
                rows: [
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "top",
                        rowSpan: 4,
                        children: [
                          new Paragraph({
                            text: "    ",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 80,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "top",
                        columnSpan: 2,
                        children: [
                          new Paragraph({
                            text: `${data.defendant_name}(${data.defendant_id})`,
                          }),
                        ],
                      }),
                    ],
                  }),
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 80,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "top",
                        columnSpan: 2,
                        children: [
                          new Paragraph({
                            text: `${data.defendant_address} (: ${data.defendant_number})`,
                          }),
                        ],
                      }),
                    ],
                  }),
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 40,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "top",
                        children: [
                          new Paragraph({
                            text: `: ${data.defendant_phone}`,
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 40,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "top",
                        children: [
                          new Paragraph({
                            text: `: ${data.defendant_fax}`,
                          }),
                        ],
                      }),
                    ],
                  }),
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 80,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "top",
                        columnSpan: 2,
                        children: [
                          new Paragraph({
                            text: `: ${data.defendant_mail}`,
                          }),
                        ],
                      }),
                    ],
                  }),
                ],
              }),
              new Paragraph(""),
              new Paragraph({
                children: [
                  new TextRun({
                    text: `${data.cattle} `,
                    bold: true,
                    size: 25,
                  }),
                ],
              }),
              new Paragraph(""),
              new Paragraph({
                alignment: "center",
                children: [
                  new TextRun({
                    text: "   ",
                    bold: true,
                    size: 25,
                  }),
                ],
              }),
              new Paragraph(""),
              ...effectElement,
              new Paragraph(""),
              new Paragraph({
                alignment: "center",
                children: [
                  new TextRun({
                    text: "   ",
                    bold: true,
                    size: 25,
                  }),
                ],
              }),
              new Paragraph(""),
              ...causeElement,
              new Paragraph(""),
              new Paragraph({
                alignment: "center",
                children: [
                  new TextRun({
                    text: "   ",
                    bold: true,
                    size: 25,
                  }),
                ],
              }),
              new Paragraph(""),
              ...proofElement,
              new Paragraph(""),
              new Paragraph({
                alignment: "center",
                children: [
                  new TextRun({
                    text: "   ",
                    bold: true,
                    size: 25,
                  }),
                ],
              }),
              new Paragraph(""),
              new Table({
                width: {
                  size: 100,
                  type: WidthType.PERCENTAGE,
                },
                borders: {
                  top: { style: "none" },
                  bottom: { style: "none" },
                  left: { style: "none" },
                  right: { style: "none" },
                  insideHorizontal: { style: "none" },
                  insideVertical: { style: "none" },
                },
                rows: [
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 30,
                          type: WidthType.PERCENTAGE,
                        },
                        children: [
                          new Paragraph({
                            text: "1.  ",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        children: [
                          new Paragraph({
                            text: " 1",
                            alignment: "right",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 50,
                          type: WidthType.PERCENTAGE,
                        },
                        children: [new Paragraph("")],
                      }),
                    ],
                  }),
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 30,
                          type: WidthType.PERCENTAGE,
                        },
                        children: [
                          new Paragraph({
                            text: "1. ",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        children: [
                          new Paragraph({
                            text: "1",
                            alignment: "right",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 50,
                          type: WidthType.PERCENTAGE,
                        },
                        children: [new Paragraph("")],
                      }),
                    ],
                  }),
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 30,
                          type: WidthType.PERCENTAGE,
                        },
                        children: [
                          new Paragraph({
                            text: "1. ",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        children: [
                          new Paragraph({
                            text: "1",
                            alignment: "right",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 50,
                          type: WidthType.PERCENTAGE,
                        },
                        children: [new Paragraph("")],
                      }),
                    ],
                  }),
                ],
              }),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph({
                text: `${data.creation_date}`,
                alignment: "center",
              }),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph({
                text: "  ",
                alignment: "center",
              }),
              new Paragraph({
                text: ` ${data.lawyer_name} (  )`,
                alignment: "center",
              }),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph({
                children: [
                  new TextRun({
                    text: `${data.courthouse} `,
                    size: 25,
                    bold: true,
                  }),
                ],
              }),
            ],
          },
        ],
      });
      Packer.toBlob(doc).then((bolb) => {
        saveAs(bolb, ".docx");
      });
    }
  }, [effectElement && causeElement && proofElement]);

  return (
    <DetailWrap submitHandler={handleSubmit(onSubmit)}>
      <Name> </Name>
      <Heading></Heading>
      <HeadingWrap>
        <Title>
          <span>:</span>
          <InputText
            type="text"
            {...register("plaintiff_name", { required: " ." })}
          />
        </Title>
        <Title>
          <span>:</span>
          <InputText
            type="text"
            {...register("plaintiff_id", {
              required: " .",
            })}
          />
        </Title>
        <Title>
          <span>:</span>
          <InputText
            type="text"
            {...register("plaintiff_address", {
              required: " .",
            })}
          />
        </Title>
        <Title>
          <span>:</span>
          <InputText
            type="text"
            {...register("plaintiff_number", {
              required: " .",
            })}
          />
        </Title>
        <Title>
          <span>:</span>
          <InputText
            type="text"
            {...register("plaintiff_phone", {
              required: " .",
            })}
          />
        </Title>
        <Title>
          <span>:</span>
          <InputText
            type="text"
            {...register("plaintiff_fax", {
              required: " .",
            })}
          />
        </Title>
        <Title>
          <span>:</span>
          <InputText
            type="email"
            {...register("plaintiff_mail", {
              required: " .",
            })}
          />
        </Title>
        <Title>
          <span>():</span>
          <InputText
            type="text"
            {...register("lawyer_name", { required: " ." })}
          />
        </Title>
        <Title>
          <span> :</span>
          <InputText
            type="text"
            {...register("lawyer_address", { required: " ." })}
          />
        </Title>
        <Title>
          <span> :</span>
          <InputText
            type="text"
            {...register("lawyer_number", {
              required: " .",
            })}
          />
        </Title>
      </HeadingWrap>

      <Heading></Heading>
      <HeadingWrap>
        <Title>
          <span>:</span>
          <InputText
            type="text"
            {...register("defendant_name", { required: " ." })}
          />
        </Title>
        <Title>
          <span>:</span>
          <InputText
            type="text"
            {...register("defendant_id", {
              required: " .",
            })}
          />
        </Title>
        <Title>
          <span>:</span>
          <InputText
            type="text"
            {...register("defendant_address", {
              required: " .",
            })}
          />
        </Title>
        <Title>
          <span>:</span>
          <InputText
            type="text"
            {...register("defendant_number", {
              required: " .",
            })}
          />
        </Title>
        <Title>
          <span>:</span>
          <InputText
            type="text"
            {...register("defendant_phone", {
              required: " .",
            })}
          />
        </Title>
        <Title>
          <span>:</span>
          <InputText
            type="text"
            {...register("defendant_fax", {
              required: " .",
            })}
          />
        </Title>
        <Title>
          <span>:</span>
          <InputText
            type="email"
            {...register("defendant_mail", {
              required: " .",
            })}
          />
        </Title>
      </HeadingWrap>
      <Heading> </Heading>
      <SelectWrap>
        <select ref={whatCattle} onChange={changeHandler}>
          <option value="claim">    </option>
          <option value="case">  </option>
        </select>
        <select {...register("cattle")}>
          {cattle === "claim" ? (
            <>
              <option>  </option>
              <option>  </option>
              <option>  </option>
              <option>   </option>
              <option>  </option>
              <option> </option>
              <option>  </option>
              <option> </option>
              <option>  </option>
              <option>  </option>
              <option>  </option>
              <option>   </option>
              <option>  </option>
              <option> </option>
              <option>  </option>
              <option>  </option>
              <option> </option>
              <option>  </option>
              <option>    </option>
              <option>  </option>
              <option>  </option>
              <option> </option>
              <option> </option>
              <option> </option>
              <option> </option>
              <option>  </option>
              <option>  </option>
              <option>  </option>
              <option>  </option>
              <option>  </option>
            </>
          ) : (
            <>
              <option>   </option>
              <option>  </option>
              <option> </option>
              <option>  </option>
              <option>   </option>
              <option>  </option>
              <option>  </option>
              <option>   </option>
              <option>  </option>
              <option>   </option>
              <option>  </option>
              <option>  </option>
              <option> </option>
              <option>  </option>
              <option> </option>
              <option>  </option>
              <option>  </option>
              <option>  </option>
              <option>  </option>
              <option>  </option>
              <option>   </option>
              <option>   </option>
              <option>   </option>
            </>
          )}
        </select>
      </SelectWrap>
      <Heading>
        
        <PlusButton onClick={effectHandler}>
          <FontAwesomeIcon icon={faPlus} />
        </PlusButton>
      </Heading>
      {effectPlus.map((data, index) => (
        <>
          <Title key={index}> {data}</Title>
          <Textarea
            cols={50}
            rows={5}
            {...register(`effect${data}`)}
          ></Textarea>
        </>
      ))}
      <Heading>
        
        <PlusButton onClick={causeHandler}>
          <FontAwesomeIcon icon={faPlus} />
        </PlusButton>
      </Heading>
      {causePlus.map((data, index) => (
        <>
          <Title key={index}> {data}</Title>
          <Textarea cols={50} rows={5} {...register(`cause${data}`)}></Textarea>
        </>
      ))}
      <Heading>
        
        <PlusButton onClick={proofHandler}>
          <FontAwesomeIcon icon={faPlus} />
        </PlusButton>
      </Heading>
      {proofPlus.map((data, index) => (
        <>
          <Title key={index}> {data}</Title>
          <Textarea cols={50} rows={5} {...register(`proof${data}`)}></Textarea>
        </>
      ))}
      <Heading></Heading>
      <InputText
        type="text"
        {...register("creation_date", { required: " ." })}
      />
      <Heading></Heading>
      <InputText
        style={{ marginBottom: "20px" }}
        type="text"
        {...register("courthouse", { required: " ." })}
      />
      <SubmitButton />
    </DetailWrap>
  );
};

export default CivilLitigationComplaint;
