import {
  Document,
  Packer,
  Paragraph,
  Table,
  TableCell,
  TableRow,
  TextRun,
  WidthType,
} from "docx";
import styled from "styled-components";
import { saveAs } from "file-saver";
import DetailWrap from "../../components/DetailWrap";
import { useForm } from "react-hook-form";
import Name from "../../components/Name";
import SubmitButton from "../../components/SubmitButton";
import { useEffect, useState } from "react";
import Title from "../../components/Title";

const SubmitInput = styled.input`
  all: unset;
  padding: 5px;
  width: 50%;
  border: 1px solid #000;
  margin-bottom: 20px;
`;

const Reminder = (): JSX.Element => {
  const [data, setData] = useState<any>(null);

  const { register, handleSubmit, reset } = useForm<any>();

  const onSubmit = (data: any) => {
    setData({
      bond_name: `${data?.bond_name}`,
      debtor_name: `${data?.debtor_name}`,
      debtor_address: `${data?.debtor_address}`,
      period: `${data?.period}`,
      borrowed_amount: `${data?.borrowed_amount}`,
      interest: `${data?.interest}`,
      etc: `${data?.etc}`,
      deadline: `${data?.deadline}`,
      creditor_name: `${data?.creditor_name}`,
      bank: `${data?.bank}`,
      account_number: `${data?.account_number}`,
      account_tel: `${data?.account_tel}`,
      creation_date: `${data?.creation_date}`,
    });
    reset();
  };

  useEffect(() => {
    if (data) {
      const doc = new Document({
        sections: [
          {
            children: [
              new Paragraph({
                alignment: "center",
                children: [
                  new TextRun({
                    text: "  ",
                    bold: true,
                    size: 30,
                  }),
                ],
              }),
              new Paragraph(""),
              new Table({
                width: {
                  size: 100,
                  type: WidthType.PERCENTAGE,
                },
                rows: [
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: "  ",
                            alignment: "center",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 80,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        columnSpan: 4,
                        children: [
                          new Paragraph({
                            text: `${data?.bond_name}`,
                            alignment: "center",
                          }),
                        ],
                      }),
                    ],
                  }),
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        rowSpan: 2,
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: "  ",
                            alignment: "center",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: "  ",
                            alignment: "center",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 60,
                          type: WidthType.PERCENTAGE,
                        },
                        columnSpan: 3,
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: `${data?.debtor_name}`,
                            alignment: "center",
                          }),
                        ],
                      }),
                    ],
                  }),
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: "  ",
                            alignment: "center",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 60,
                          type: WidthType.PERCENTAGE,
                        },
                        columnSpan: 3,
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: `${data?.debtor_address}`,
                            alignment: "center",
                          }),
                        ],
                      }),
                    ],
                  }),
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: "",
                            alignment: "center",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 80,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        columnSpan: 4,
                        children: [
                          new Paragraph({
                            text: `${data?.period}`,
                            alignment: "center",
                          }),
                        ],
                      }),
                    ],
                  }),
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        rowSpan: 2,
                        children: [
                          new Paragraph({
                            text: "",
                            alignment: "center",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: "   ",
                            alignment: "center",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: "  ",
                            alignment: "center",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: "",
                            alignment: "center",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: "",
                            alignment: "center",
                          }),
                        ],
                      }),
                    ],
                  }),
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: `${data?.borrowed_amount}`,
                            alignment: "center",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: `${data?.interest}`,
                            alignment: "center",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: `${data?.etc}`,
                            alignment: "center",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: `${
                              parseFloat(data?.borrowed_amount) +
                              parseFloat(data?.interest) +
                              parseFloat(data?.etc)
                            }`,
                            alignment: "center",
                          }),
                        ],
                      }),
                    ],
                  }),
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: "",
                            alignment: "center",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 80,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        columnSpan: 4,
                        children: [
                          new Paragraph({
                            text: `${data?.deadline}`,
                            alignment: "center",
                          }),
                        ],
                      }),
                    ],
                  }),
                  new TableRow({
                    height: {
                      value: 4000,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 100,
                          type: WidthType.PERCENTAGE,
                        },
                        columnSpan: 5,
                        children: [
                          new Paragraph(""),
                          new Paragraph({
                            text: `     ${data?.bank}: ${data?.account_number}   .`,
                            alignment: "center",
                          }),
                          new Paragraph(""),
                          new Paragraph(""),
                          new Paragraph(""),
                          new Paragraph({
                            text: `${data?.creation_date}`,
                            alignment: "center",
                          }),
                          new Paragraph(""),
                          new Paragraph(""),
                          new Paragraph(""),
                          new Paragraph({
                            text: " : ________()",
                            alignment: "right",
                          }),
                          new Paragraph(""),
                        ],
                      }),
                    ],
                  }),
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 100,
                          type: WidthType.PERCENTAGE,
                        },
                        columnSpan: 5,
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: `     ${data?.creditor_name}( : ${data?.account_tel})  .`,
                            alignment: "center",
                          }),
                        ],
                      }),
                    ],
                  }),
                ],
              }),
            ],
          },
        ],
      });
      Packer.toBlob(doc).then((bolb) => {
        saveAs(bolb, ".docx");
        console.log("  .");
      });
    }
  }, [data]);

  return (
    <DetailWrap submitHandler={handleSubmit(onSubmit)}>
      <Name></Name>
      <Title></Title>
      <SubmitInput type="text" {...register("bond_name")} />
      <Title> </Title>
      <SubmitInput type="text" {...register("debtor_name")} />
      <Title> </Title>
      <SubmitInput type="text" {...register("debtor_address")} />
      <Title></Title>
      <SubmitInput type="text" {...register("period")} />
      <Title></Title>
      <SubmitInput type="number" {...register("borrowed_amount")} />
      <Title></Title>
      <SubmitInput type="number" {...register("interest")} />
      <Title> </Title>
      <SubmitInput type="number" {...register("etc")} />
      <Title></Title>
      <SubmitInput type="text" {...register("deadline")} />
      <Title> </Title>
      <SubmitInput type="text" {...register("creditor_name")} />
      <Title> </Title>
      <SubmitInput type="text" {...register("bank")} />
      <Title> </Title>
      <SubmitInput type="text" {...register("account_number")} />
      <Title> </Title>
      <SubmitInput type="text" {...register("account_tel")} />
      <Title></Title>
      <SubmitInput type="text" {...register("creation_date")} />
      <SubmitButton />
    </DetailWrap>
  );
};

export default Reminder;
