import {
  Document,
  Packer,
  Paragraph,
  Table,
  TableCell,
  TableRow,
  TextRun,
  WidthType,
} from "docx";
import { saveAs } from "file-saver";
import DetailWrap from "../../components/DetailWrap";
import SubmitButton from "../../components/SubmitButton";
import Name from "../../components/Name";
import Title from "../../components/Title";
import { useForm } from "react-hook-form";
import styled from "styled-components";
import { useEffect, useState } from "react";

const SubmitInput = styled.input`
  all: unset;
  padding: 5px;
  width: 50%;
  border: 1px solid #000;
  margin-bottom: 20px;
`;

const IOU = (): JSX.Element => {
  const [data, setData] = useState<any>(null);

  const {
    register,
    handleSubmit,
    reset,
  } = useForm<any>();

  const onSubmit = (data: any) => {
    setData({
      debtor_name: `${data.debtor_name}`,
      debtor_birth: `${data.debtor_birth}`,
      debtor_address: `${data.debtor_address}`,
      creditor_name: `${data.creditor_name}`,
      creditor_birth: `${data.creditor_birth}`,
      creditor_address: `${data.creditor_address}`,
      borrowed_amount: `${data.borrowed_amount}`,
      interest: `${data.interest}`,
      method: `${data.method}`,
      borrowing_date: `${data.borrowing_date}`,
      repayment_date: `${data.repayment_date}`,
      bank: `${data.bank}`,
      account_number: `${data.account_number}`,
      overdue: `${data.overdue}`,
      creation_date: `${data.creation_date}`,
    });
    reset();
  };

  useEffect(() => {
    if (data) {
      const doc = new Document({
        sections: [
          {
            children: [
              new Paragraph({
                children: [
                  new TextRun({
                    text: "",
                    bold: true,
                    size: 30,
                  }),
                ],
                alignment: "center",
              }),
              new Paragraph(""),
              new Table({
                width: {
                  size: 100,
                  type: WidthType.PERCENTAGE,
                },
                rows: [
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 10,
                          type: WidthType.PERCENTAGE,
                        },
                        children: [
                          new Paragraph({
                            text: "",
                            alignment: "center",
                          }),
                        ],
                        verticalAlign: "center",
                        rowSpan: 2,
                      }),
                      new TableCell({
                        width: {
                          size: 10,
                          type: WidthType.PERCENTAGE,
                        },
                        children: [
                          new Paragraph({
                            text: "",
                            alignment: "center",
                          }),
                        ],
                        verticalAlign: "center",
                      }),
                      new TableCell({
                        width: {
                          size: 30,
                          type: WidthType.PERCENTAGE,
                        },
                        children: [
                          new Paragraph({
                            text: `${data?.debtor_name}`,
                            alignment: "center",
                          }),
                        ],
                        verticalAlign: "center",
                        columnSpan: 2,
                      }),
                      new TableCell({
                        width: {
                          size: 10,
                          type: WidthType.PERCENTAGE,
                        },
                        children: [
                          new Paragraph({
                            text: "",
                            alignment: "center",
                          }),
                        ],
                        verticalAlign: "center",
                      }),
                      new TableCell({
                        width: {
                          size: 40,
                          type: WidthType.PERCENTAGE,
                        },
                        children: [
                          new Paragraph({
                            text: `${data?.debtor_birth}`,
                            alignment: "center",
                          }),
                        ],
                        verticalAlign: "center",
                        columnSpan: 3,
                      }),
                    ],
                  }),
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 10,
                          type: WidthType.PERCENTAGE,
                        },
                        children: [
                          new Paragraph({
                            text: "",
                            alignment: "center",
                          }),
                        ],
                        verticalAlign: "center",
                      }),
                      new TableCell({
                        width: {
                          size: 80,
                          type: WidthType.PERCENTAGE,
                        },
                        children: [
                          new Paragraph({
                            text: `${data?.debtor_address}`,
                            alignment: "center",
                          }),
                        ],
                        verticalAlign: "center",
                        columnSpan: 5,
                      }),
                    ],
                  }),
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 10,
                          type: WidthType.PERCENTAGE,
                        },
                        children: [
                          new Paragraph({
                            text: "",
                            alignment: "center",
                          }),
                        ],
                        verticalAlign: "center",
                        rowSpan: 2,
                      }),
                      new TableCell({
                        width: {
                          size: 10,
                          type: WidthType.PERCENTAGE,
                        },
                        children: [
                          new Paragraph({
                            text: "",
                            alignment: "center",
                          }),
                        ],
                        verticalAlign: "center",
                      }),
                      new TableCell({
                        width: {
                          size: 30,
                          type: WidthType.PERCENTAGE,
                        },
                        children: [
                          new Paragraph({
                            text: `${data?.creditor_name}`,
                            alignment: "center",
                          }),
                        ],
                        verticalAlign: "center",
                        columnSpan: 2,
                      }),
                      new TableCell({
                        width: {
                          size: 10,
                          type: WidthType.PERCENTAGE,
                        },
                        children: [
                          new Paragraph({
                            text: "",
                            alignment: "center",
                          }),
                        ],
                        verticalAlign: "center",
                      }),
                      new TableCell({
                        width: {
                          size: 40,
                          type: WidthType.PERCENTAGE,
                        },
                        children: [
                          new Paragraph({
                            text: `${data?.creditor_birth}`,
                            alignment: "center",
                          }),
                        ],
                        verticalAlign: "center",
                        columnSpan: 3,
                      }),
                    ],
                  }),
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 10,
                          type: WidthType.PERCENTAGE,
                        },
                        children: [
                          new Paragraph({
                            text: "",
                            alignment: "center",
                          }),
                        ],
                        verticalAlign: "center",
                      }),
                      new TableCell({
                        width: {
                          size: 90,
                          type: WidthType.PERCENTAGE,
                        },
                        children: [
                          new Paragraph({
                            text: `${data?.creditor_address}`,
                            alignment: "center",
                          }),
                        ],
                        verticalAlign: "center",
                        columnSpan: 5,
                      }),
                    ],
                  }),
                ],
              }),
              new Paragraph(""),
              new Table({
                width: {
                  size: 100,
                  type: WidthType.PERCENTAGE,
                },
                rows: [
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: "",
                            alignment: "center",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 80,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        columnSpan: 5,
                        children: [
                          new Paragraph({
                            text: ` ${data?.borrowed_amount}( ${data?.borrowed_amount})`,
                            alignment: "left",
                          }),
                        ],
                      }),
                    ],
                  }),
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: "",
                            alignment: "center",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 30,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        columnSpan: 2,
                        children: [
                          new Paragraph({
                            text: `${data?.interest}`,
                            alignment: "center",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: "",
                            alignment: "center",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 30,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        columnSpan: 2,
                        children: [
                          new Paragraph({
                            text: `${data?.method}`,
                            alignment: "center",
                          }),
                        ],
                      }),
                    ],
                  }),
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: "",
                            alignment: "center",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 30,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        columnSpan: 2,
                        children: [
                          new Paragraph({
                            text: `${data?.borrowing_date}`,
                            alignment: "center",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: "",
                            alignment: "center",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 30,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        columnSpan: 2,
                        children: [
                          new Paragraph({
                            text: `${data?.repayment_date}`,
                            alignment: "center",
                          }),
                        ],
                      }),
                    ],
                  }),
                ],
              }),
              new Paragraph(""),
              new Paragraph({
                alignment: "center",
                children: [
                  new TextRun({
                    text: "         .",
                  }),
                ],
              }),
              new Paragraph(""),
              new Paragraph({
                alignment: "center",
                children: [
                  new TextRun({
                    text: "    ",
                    bold: true,
                    size: 25,
                  }),
                ],
              }),
              new Paragraph({
                alignment: "left",
                children: [
                  new TextRun({
                    text: "1.         .",
                  }),
                ],
              }),
              new Paragraph({
                alignment: "left",
                children: [
                  new TextRun({
                    text: `  ( ${data?.bank}, ${data?.debtor_name}, ${data?.account_number})`,
                  }),
                ],
              }),
              new Paragraph({
                alignment: "left",
                children: [
                  new TextRun({
                    text: `2.  ${data?.repayment_date}  . ,     .`,
                  }),
                ],
              }),
              new Paragraph({
                alignment: "left",
                children: [
                  new TextRun({
                    text: "3.      .",
                  }),
                ],
              }),
              new Paragraph({
                alignment: "left",
                children: [
                  new TextRun({
                    text: `4.   ${data?.overdue}               .`,
                  }),
                ],
              }),
              new Paragraph(""),
              new Paragraph({
                alignment: "left",
                children: [
                  new TextRun({
                    text: "    ,     .",
                  }),
                ],
              }),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph({
                alignment: "center",
                children: [
                  new TextRun({
                    text: `${data?.creation_date}`,
                  }),
                ],
              }),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph({
                alignment: "left",
                children: [
                  new TextRun({
                    text: "                                                                                                                                            :_________()",
                  }),
                ],
              }),
              new Paragraph({
                alignment: "left",
                children: [
                  new TextRun({
                    text: "                                                                                                                                            :",
                  }),
                ],
              }),
            ],
          },
        ],
      });
      Packer.toBlob(doc).then((blob) => {
        saveAs(blob, ".docx");
      });
    }
  }, [data]);

  return (
    <DetailWrap submitHandler={handleSubmit(onSubmit)}>
      <Name></Name>
      <Title> </Title>
      <SubmitInput type="text" {...register("debtor_name")} />
      <Title> </Title>
      <SubmitInput type="date" {...register("debtor_birth")} />
      <Title> </Title>
      <SubmitInput type="text" {...register("debtor_address")} />
      <Title> </Title>
      <SubmitInput type="text" {...register("creditor_name")} />
      <Title> </Title>
      <SubmitInput type="date" {...register("creditor_birth")} />
      <Title> </Title>
      <SubmitInput type="text" {...register("creditor_address")} />
      <Title></Title>
      <SubmitInput type="number" {...register("borrowed_amount")} />
      <Title></Title>
      <SubmitInput type="number" {...register("interest")} />
      <Title></Title>
      <SubmitInput type="text" {...register("method")} />
      <Title></Title>
      <SubmitInput type="text" {...register("borrowing_date")} />
      <Title></Title>
      <SubmitInput type="text" {...register("repayment_date")} />
      <Title> </Title>
      <SubmitInput type="text" {...register("bank")} />
      <Title> </Title>
      <SubmitInput type="text" {...register("account_number")} />
      <Title></Title>
      <SubmitInput type="text" {...register("overdue")} />
      <Title></Title>
      <SubmitInput type="text" {...register("creation_date")} />
      <SubmitButton />
    </DetailWrap>
  );
};

export default IOU;
