import {
  Document,
  Packer,
  Paragraph,
  Table,
  TableCell,
  TableRow,
  TextRun,
  WidthType,
} from "docx";
import { saveAs } from "file-saver";
import styled from "styled-components";
import DetailWrap from "../../components/DetailWrap";
import { useForm } from "react-hook-form";
import Name from "../../components/Name";
import Title from "../../components/Title";
import { useEffect, useState } from "react";
import SubmitButton from "../../components/SubmitButton";

const SubmitInput = styled.input`
  all: unset;
  padding: 5px;
  width: 50%;
  border: 1px solid #000;
  margin-bottom: 20px;
`;

const Memorandum = (): JSX.Element => {
  const [data, setData] = useState<any>(null);

  const { register, handleSubmit, reset } = useForm<any>();

  const onSubmit = (data: any) => {
    setData({
      debtor_name: `${data.debtor_name}`,
      debtor_id: `${data.debtor_id}`,
      debtor_number: `${data.debtor_number}`,
      debtor_address: `${data.debtor_address}`,
      borrowing_date: `${data.borrowing_date}`,
      borrowed_amount: `${data.borrowed_amount}`,
      repayment_date: `${data.repayment_date}`,
      pawn: `${data.pawn}`,
      creation_date: `${data.creation_date}`,
      creditor_name: `${data.creditor_name}`,
    });
    reset();
  };

  useEffect(() => {
    if (data) {
      const doc = new Document({
        sections: [
          {
            children: [
              new Paragraph({
                alignment: "center",
                children: [
                  new TextRun({
                    text: "    ",
                    bold: true,
                    size: 30,
                  }),
                ],
              }),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph({
                children: [
                  new TextRun({
                    text: "  ",
                    bold: true,
                  }),
                ],
              }),
              new Table({
                width: {
                  size: 100,
                  type: WidthType.PERCENTAGE,
                },
                rows: [
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        rowSpan: 4,
                        children: [
                          new Paragraph({
                            text: "",
                            alignment: "center",
                          }),
                          new Paragraph({
                            text: "",
                            alignment: "center",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: "    ",
                            alignment: "center",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 60,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: `${data?.debtor_name}`,
                            alignment: "center",
                          }),
                        ],
                      }),
                    ],
                  }),
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: "",
                            alignment: "center",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 60,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: `${data.debtor_id}`,
                            alignment: "center",
                          }),
                        ],
                      }),
                    ],
                  }),
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: "",
                            alignment: "center",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 60,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: `${data.debtor_number}`,
                            alignment: "center",
                          }),
                        ],
                      }),
                    ],
                  }),
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children: [
                      new TableCell({
                        width: {
                          size: 20,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: "    ",
                            alignment: "center",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 60,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children: [
                          new Paragraph({
                            text: `${data.debtor_address}`,
                            alignment: "center",
                          }),
                        ],
                      }),
                    ],
                  }),
                ],
              }),
              new Paragraph(""),
              new Paragraph({
                children: [
                  new TextRun({
                    text: "   ",
                    bold: true,
                  }),
                  new TextRun({
                    text: " :    ",
                  }),
                ],
              }),
              new Paragraph(""),
              new Paragraph(
                `   ${data?.borrowing_date}  ${data?.borrowed_amount}  ${data?.repayment_date}       ${data?.repayment_date}      ${data?.pawn}  (   )                 .`
              ),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph({
                text: `${data?.creation_date}`,
                alignment: "center",
              }),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph({
                text: "  : ________()",
                alignment: "right",
              }),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph({
                children: [
                  new TextRun({
                    text: `${data?.creditor_name} `,
                    size: 25,
                    bold: true,
                  }),
                ],
              }),
            ],
          },
        ],
      });
      Packer.toBlob(doc).then((bolb) => {
        saveAs(bolb, "(   ).docx");
      });
    }
  }, [data]);

  return (
    <DetailWrap submitHandler={handleSubmit(onSubmit)}>
      <Name></Name>
      <Title> </Title>
      <SubmitInput type="text" {...register("debtor_name")} />
      <Title> </Title>
      <SubmitInput type="text" {...register("debtor_id")} />
      <Title> </Title>
      <SubmitInput type="text" {...register("debtor_number")} />
      <Title> </Title>
      <SubmitInput type="text" {...register("debtor_address")} />
      <Title> </Title>
      <SubmitInput type="text" {...register("creditor_name")} />
      <Title></Title>
      <SubmitInput type="text" {...register("borrowing_date")} />
      <Title></Title>
      <SubmitInput type="number" {...register("borrowed_amount")} />
      <Title></Title>
      <SubmitInput type="text" {...register("repayment_date")} />
      <Title></Title>
      <SubmitInput type="text" {...register("pawn")} />
      <Title></Title>
      <SubmitInput type="text" {...register("creation_date")} />
      <SubmitButton />
    </DetailWrap>
  );
};

export default Memorandum;
