import { Document, Packer, Paragraph, Table, TableCell, TableRow, TextRun, WidthType } from "docx";
import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import DetailWrap from "../../components/DetailWrap";
import Name from "../../components/Name";
import Title from "../../components/Title";
import styled from "styled-components";
import SubmitButton from "../../components/SubmitButton";
import { saveAs } from "file-saver";

const InputText = styled.input`
  all: unset;
  padding: 5px;
  width: 50%;
  border: 1px solid #000;
  margin-bottom: 20px;
`;

const CriminalWithdrawn = (): JSX.Element => {
  const [data, setData] = useState<any>();

  const { register, handleSubmit, reset } = useForm<any>();

  const onSubmit = (data:any) => {
    setData({
      delator_name: `${data.delator_name}`,
      respondent_name: `${data.respondent_name}`,
      complaint_date: `${data.complaint_date}`,
      charge: `${data.charge}`,
      creation_date: `${data.creation_date}`,
      department: `${data.department}`,
    });
    reset();
  };

  useEffect(() => {
    if (data) {
      const doc = new Document({
        sections: [
          {
            children:[
              new Paragraph({
                alignment: "center",
                children: [
                  new TextRun({
                    text: "    ",
                    bold: true,
                    size: 30,
                  }),
                ],
              }),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Table({
                width: {
                  size: 100,
                  type: WidthType.PERCENTAGE,
                },
                borders: {
                  top: { style: "none" },
                  bottom: { style: "none" },
                  left: { style: "none" },
                  right: { style: "none" },
                  insideHorizontal: { style: "none" },
                  insideVertical: { style: "none" },
                },
                rows:[
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children:[
                      new TableCell({
                        width: {
                          size: 15,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children:[
                          new Paragraph({
                            text: "  ",
                            alignment: "left",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 85,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children:[
                          new Paragraph({
                            text: `${data.delator_name}`,
                            alignment: "left",
                          }),
                        ],
                      }),
                    ]
                  }),
                  new TableRow({
                    height: {
                      value: 700,
                      rule: "exact",
                    },
                    children:[
                      new TableCell({
                        width: {
                          size: 15,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children:[
                          new Paragraph({
                            text: "",
                            alignment: "left",
                          }),
                        ],
                      }),
                      new TableCell({
                        width: {
                          size: 85,
                          type: WidthType.PERCENTAGE,
                        },
                        verticalAlign: "center",
                        children:[
                          new Paragraph({
                            text: `${data.respondent_name}`,
                            alignment: "left",
                          }),
                        ],
                      }),
                    ]
                  }),
                ],
              }),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph({
                text: ` ${data.complaint_date}  ${data.charge}          .`,
              }),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph({
                text: `${data.creation_date}`,
                alignment: "center",
              }),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph({
                text: `   ${data.delator_name} (  )`,
                alignment: "center",
              }),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph({
                children: [
                  new TextRun({
                    text: `${data.department} `,
                    bold: true,
                    size: 25,
                  }),
                ],
              }),
            ],
          },
        ],
      });
      Packer.toBlob(doc).then((bolb) => {
        saveAs(bolb, ".docx");
      });
    }
  }, [data]);

  return (
    <DetailWrap submitHandler={handleSubmit(onSubmit)}>
      <Name></Name>
      <Title> </Title>
      <InputText
        type="text"
        {...register("delator_name", { required: " ." })}
      />
      <Title> </Title>
      <InputText
        type="text"
        {...register("respondent_name", { required: " ." })}
      />
      <Title></Title>
      <InputText
        type="date"
        {...register("complaint_date", { required: " ." })}
      />
      <Title></Title>
      <InputText
        type="text"
        {...register("charge", { required: " ." })}
      />
      <Title></Title>
      <InputText type="text" {...register("creation_date", { required: " ." })} />
      <Title></Title>
      <InputText
        type="text"
        {...register("department", { required: " ." })}
        placeholder=") OO / OO"
      />
      <SubmitButton />
    </DetailWrap>
  );
};

export default CriminalWithdrawn;
