import { Document, Packer, Paragraph, TextRun } from "docx";
import { saveAs } from "file-saver";
import styled from "styled-components";
import DetailWrap from "../../components/DetailWrap";
import { useForm } from "react-hook-form";
import Name from "../../components/Name";
import Title from "../../components/Title";
import SubmitButton from "../../components/SubmitButton";
import { useEffect, useState } from "react";

const SubmitInput = styled.input`
  all: unset;
  padding: 5px;
  width: 50%;
  border: 1px solid #000;
  margin-bottom: 20px;
`;

const Waiver = (): JSX.Element => {
  const [data, setData] = useState<any>(null);

  
  const {register, handleSubmit, reset} = useForm<any>();
  
  const onSubmit = (data:any) => {
    setData({
      debtor_name: `${data.debtor_name}`,
      debtor_id: `${data.debtor_id}`,
      debtor_address: `${data.debtor_address}`,
      debtor_number: `${data.debtor_number}`,
      creditor_name: `${data.creditor_name}`,
      borrowed_amount: `${data.borrowed_amount}`,
      repayment_date: `${data.repayment_date}`,
      collateral: `${data.collateral}`,
      creation_date: `${data.creation_date}`,
    });
    reset();
  };
  
  useEffect(()=>{
    if(data) {
      const doc = new Document({
        sections: [
          {
            children: [
              new Paragraph({
                alignment: "center",
                children: [
                  new TextRun({
                    text: "",
                    bold: true,
                    size: 30,
                  }),
                ],
              }),
              new Paragraph({
                children: [
                  new TextRun({
                    text: "  ",
                    bold: true,
                  }),
                ],
              }),
              new Paragraph({
                children: [
                  new TextRun({
                    text: `1.      : ${data?.debtor_name}`,
                  }),
                ],
              }),
              new Paragraph({
                children: [
                  new TextRun({
                    text: `2.  : ${data?.debtor_id}`,
                  }),
                ],
              }),
              new Paragraph({
                children: [
                  new TextRun({
                    text: `3.      : ${data?.debtor_address}`,
                  }),
                ],
              }),
              new Paragraph({
                children: [
                  new TextRun({
                    text: `4.     : ${data?.debtor_number}`,
                  }),
                ],
              }),
              new Paragraph(""),
              new Paragraph({
                children: [
                  new TextRun({
                    text: "     ",
                    bold: true,
                  }),
                  new TextRun({
                    text: ` :  ${data?.borrowed_amount} ( ${data?.borrowed_amount})`,
                  }),
                ],
              }),
              new Paragraph({
                children: [
                  new TextRun({
                    text: " ",
                    bold: true,
                  }),
                  new TextRun({
                    text: ` : ${data?.repayment_date}`,
                  }),
                ],
              }),
              new Paragraph(""),
              new Paragraph({
                children: [
                  new TextRun({
                    text: `(${data?.debtor_name}) (${data?.creditor_name})     .`,
                  }),
                ],
              }),
              new Paragraph(""),
              new Paragraph({
                children: [
                  new TextRun({
                    text: "            .",
                  }),
                ],
              }),
              new Paragraph(""),
              new Paragraph({
                children: [
                  new TextRun({
                    text: `  : ${data?.collateral}`,
                    bold: true,
                  }),
                  new TextRun({
                    text: "",
                  }),
                ],
              }),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph({
                alignment: "center",
                children: [
                  new TextRun({
                    text: `${data?.creation_date}`,
                  }),
                ],
              }),
              new Paragraph(""),
              new Paragraph(""),
              new Paragraph({
                alignment: "left",
                children: [
                  new TextRun({
                    text: "                                                                                                                          : __________()",
                  }),
                ],
              }),
              new Paragraph({
                alignment: "left",
                children: [
                  new TextRun({
                    text: "                                                                                                                         :",
                  }),
                ],
              }),
              new Paragraph({
                alignment: "left",
                children: [
                  new TextRun({
                    text: "                                                                                                                         :",
                  }),
                ],
              }),
              new Paragraph({
                alignment: "left",
                children: [
                  new TextRun({
                    text: "                                                                                                                         :",
                  }),
                ],
              }),
              new Paragraph({
                alignment: "left",
                children: [
                  new TextRun({
                    text: "                                                                                                                          : __________()",
                  }),
                ],
              }),
              new Paragraph({
                alignment: "left",
                children: [
                  new TextRun({
                    text: "                                                                                                                         :",
                  }),
                ],
              }),
              new Paragraph({
                alignment: "left",
                children: [
                  new TextRun({
                    text: "                                                                                                                         :",
                  }),
                ],
              }),
              new Paragraph({
                alignment: "left",
                children: [
                  new TextRun({
                    text: "                                                                                                                         :",
                  }),
                ],
              }),
              new Paragraph({
                alignment: "left",
                children: [
                  new TextRun({
                    text: "                                                                                                                          : __________()",
                  }),
                ],
              }),
              new Paragraph({
                alignment: "left",
                children: [
                  new TextRun({
                    text: "                                                                                                                         :",
                  }),
                ],
              }),
              new Paragraph({
                alignment: "left",
                children: [
                  new TextRun({
                    text: "                                                                                                                         :",
                  }),
                ],
              }),
              new Paragraph({
                alignment: "left",
                children: [
                  new TextRun({
                    text: "                                                                                                                         :",
                  }),
                ],
              }),
            ],
          },
        ],
      });
      Packer.toBlob(doc).then((bolb) => {
        saveAs(bolb, "(     ).docx");
      });
    }
  }, [data]);

  return (
    <DetailWrap submitHandler={handleSubmit(onSubmit)}>
      <Name></Name>
      <Title> </Title>
      <SubmitInput type="text" {...register("debtor_name")} />
      <Title> </Title>
      <SubmitInput type="text" {...register("debtor_id")} />
      <Title> </Title>
      <SubmitInput type="text" {...register("debtor_address")} />
      <Title> </Title>
      <SubmitInput type="text" {...register("debtor_number")} />
      <Title> </Title>
      <SubmitInput type="text" {...register("creditor_name")} />
      <Title></Title>
      <SubmitInput type="number" {...register("borrowed_amount")} />
      <Title></Title>
      <SubmitInput type="text" {...register("repayment_date")} />
      <Title></Title>
      <SubmitInput type="text" {...register("collateral")} />
      <Title></Title>
      <SubmitInput type="text" {...register("creation_date")} />
      <SubmitButton />
    </DetailWrap>
  );
};

export default Waiver;
